.num-clock.ink,
.vertical-clock.ink {
  text-shadow: 0 0 .1em rgba(0, 0, 0, .2),
               0 0 .05em rgba(0, 0, 0, .2),
               0 0 1px rgba(0, 0, 0, .2);
}
.dark .num-clock.ink {
  text-shadow: 0 0 .1em rgba(128, 128, 128, .2),
               0 0 .05em rgba(128, 128, 128, .2),
               0 0 1px rgba(128, 128, 128, .2);
}
.num-clock > .sep-card {
  transition: opacity 0.4s;
}
.num-clock > .sep-card.hide {
  opacity: 0;
}
.vertical-clock {
  flex-wrap: wrap;
}
.vertical-clock .num-card {
  line-height: .8em;
  width: 100%;
}
.vertical-clock .hours {
  color: var(--second-color);
}