@font-face {
  font-family: "UnicaOne";
  src: url("./UnicaOne-Regular.ttf");
}
:root {
  --primary-color   : #33333F;                  /* 文字颜色 */
  --second-color    : #e44d4e;                  /* 链接颜色 */
  --background-color: #F3F3F8;                  /* 背景颜色 */
  --card-background : rgba(255, 255, 255, .85); /* 卡片背景 */
  --border-color    : rgba(255, 255, 255, .3);  /* 卡片背景 */
  --light-green     : #9fc9ad;                  /* 开始状态 */
  --light-blue      : #95bad3;                  /* 进行状态 */
  --light-yellow    : #d3bc95;                  /* 将要状态 */
  --light-pink      : #f1bdc9;                  /* 完成状态 */
  --green           : #307f4a;                  /* 信息状态 */
  --blue            : #00aeec;                  /* 信息状态 */
  --yellow          : #ffc300;                  /* 警告状态 */
  --pink            : #fb7299;                  /* 出错状态 */
}
.dark {
  --primary-color   : #88888A;                  /* 文字颜色 */
  --second-color    : #A64848;                  /* 链接颜色 */
  --background-color: #121218;                  /* 背景颜色 */
  --card-background : rgba(36, 36, 36, .85); /* 卡片背景 */
  --border-color    : rgba(36, 36, 36, .3);  /* 卡片背景 */
  --light-green     : #2b362b;                  /* 开始状态 */
  --light-blue      : #30393e;                  /* 进行状态 */
  --light-yellow    : #2c2822;                  /* 将要状态 */
  --light-pink      : #382f31;                  /* 完成状态 */
  --green           : #255f38;                  /* 信息状态 */
  --blue            : #24586b;                  /* 信息状态 */
  --yellow          : #896a04;                  /* 警告状态 */
  --pink            : #9d445e;                  /* 出错状态 */
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
  line-height: 1.8em;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}
a {
  text-decoration: none;
  color: var(--second-color);
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.letter-info {
  font-family: "UnicaOne";
}

.dark .random-pic,
.dark .max-image {
  filter: brightness(0.6);
}