.card {
  z-index: 1;
  border-radius: 6px;
  overflow: hidden;
  position: relative;
}
.card.normal {
  background-color: var(--card-background);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}
.card.justBorder {
  border: 2px solid var(--border-color);
}
.card.borderBlur {
  border: 2px solid var(--border-color);
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px);
}
.card.verticalLine,
.card.horizontalLine {
  display: flex;
  justify-content: center;
  align-items: center;
  border: none !important;
  background: none !important;
}
.card > .line {
  background-color: var(--border-color);
  border-radius: 6px;
}
.card.verticalLine > .line {
  width: 2px;
  height: 90%;
}
.card.horizontalLine > .line {
  width: 90%;
  height: 2px;
}
.card.full {
  position: absolute;
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1000;
  border: none;
  border-radius: 0;
}
.card.background {
  z-index: 0;
}

.card .title {
  margin: 0;
  padding: 0 1rem;
}

.state {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.state.point {
  left: .5rem;
  top: .5rem;
  width: 1rem;
  height: 1rem;
  border-radius: 1rem;
}
.state.flag {
  left: auto;
  right: -1rem;
  top: -1rem;
  width: 2rem;
  height: 2rem;
  transform: rotate(-45deg);
}
.state.normal {
  background: none;
}
.state.start {
  background: var(--light-green);
}
.state.doing {
  background: var(--light-blue);
}
.state.will {
  background: var(--light-yellow);
}
.state.end {
  background: var(--light-pink);
}
.state.info {
  background: var(--blue);
}
.state.warn {
  background: var(--yellow);
}
.state.error {
  background: var(--pink);
}

.max-font-single-line {
  white-space: nowrap;
}